@import url('https://fonts.googleapis.com/css2?family=DynaPuff:wght@600&family=Rubik+Maze&display=swap');
@import 'animate.css';

@media only screen and (min-width: 700px){
.home_main{
    /* display: flex; */
        /* box-shadow: 10rem 10rem 10rem 1rem rgba(0, 0, 0, 0.59); */

    position: relative;
}
.main_text{
    height: 100vh;
    display: flex;
    position: absolute;
    z-index: 1000;
    align-items: center;
    box-shadow: 10rem 0rem 10rem 2rem rgba(0, 0, 0, 0.59);
    background-color: rgba(0, 0, 0, 0.608);
    /* background-image: linear-gradient(to right, rgb(0, 0, 0) , rgba(0, 0, 255, 0.132)); */
}
.text{
    margin-left:1.5rem ;
    /* background-color: rgba(255, 0, 0, 0.289); */
    background-color: transparent;

}
.text>div:nth-child(1){
    font-size: 2.5rem;
    margin-bottom: 0.7rem;
    background-color: transparent;
}
.text>div:nth-child(3){
    font-size: 1rem;
    margin-top: 0.7rem;
    color: grey;
    background-color: transparent;
}
.name{background-color: transparent;}
.name>span{
    /* font-family: 'Rubik Maze', cursive; */
    font-family: 'DynaPuff', cursive;
    font-size: 4rem;
    margin: 0rem 0.2rem 0rem 0.2rem;
    color: #B47124;
    background-color: transparent;
}
span{
    animation: bounce; 
    animation-duration: 2s; 
    background-color: transparent;
}
.game{
    position: absolute;
    top: 0;
    left: 0%;
    cursor: none;
    /* z-index: -100; */
}
.main_button{
    display: flex;
    margin-top: 1.5rem;
    background-color: transparent;
}
.resume_down{
    border: none;
    width: 6rem;
    height: 3rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #B47124;
    border-radius: 5px;
    margin-right: 2rem;
    text-align: center;
}
.prof_but{
    /* position: relative;
    top: 50%;
    transform: translateY(-50%); */
    color: white;
}
a{
    text-decoration: none;
    background-color: #B47124;

}
}
@media only screen and (max-width: 700px){
    .home_main{
        display: flex;
        flex-direction: column-reverse;
        position: none;
    }
    .main_text{
        height: 50vh;
    display: flex;
    align-items: center;
    box-shadow: 10rem 10rem 10rem 1rem rgba(0, 0, 0, 0.59);
    background-color: rgba(0, 0, 0, 0.608);
    }
    .text{
        margin-left:1.5rem ;
        /* background-color: rgba(255, 0, 0, 0.289); */
        background-color: linear-gradient(to right,red,white);
    
    }
    .text>div:nth-child(1){
        font-size: 2.5rem;
        margin-bottom: 0.7rem;
    }
    .text>div:nth-child(3){
        font-size: 1rem;
        margin-top: 0.7rem;
        color: grey;
    }
    .name>span{
        /* font-family: 'Rubik Maze', cursive; */
        font-family: 'DynaPuff', cursive;
        font-size: 4rem;
        margin: 0rem 0.2rem 0rem 0.2rem;
        color: #B47124;
    }
    span{
        animation: bounce; 
        animation-duration: 2s; 
    }
    .game{
        left: 0%;
        cursor: none;
    }
    .main_button{
        display: flex;
        margin-top: 1.5rem;
    }
    .resume_down{
        border: none;
        width: 6rem;
        height: 3rem;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #B47124;
        border-radius: 5px;
        margin-right: 2rem;
        text-align: center;
    }
    a{
        text-decoration: none;
        background-color: #B47124;
    
    }
}